import Vue from 'vue'
import VueRouter from 'vue-router'
import Pilars from '@/views/Pilars.vue'

Vue.use(VueRouter)

const routes = [
	{
		path: '/',
		component: Pilars,
	},
	{
		path:'/todos',
		component: () => import('@/views/Promoutes.vue')
	}	
]

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
})

export default router